
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'


export default {
components: {
Tabbar,
TabbarItem
},
props: ['healthActive', 'familyActive', 'userActive'],
data() {
return {
selected: {
healthActive: this.healthActive,
userActive: this.userActive,
familyActive: this.familyActive
}
}
}
}



